*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html, body {
  height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
  line-height: 1.5;
}

img, picture, video, canvas, svg {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

#root, #__next {
  isolation: isolate;
}

body {
  flex-direction: column;
  align-items: center;
  font-family: Helvetica Neue, sans-serif;
  display: flex;
}

main {
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  display: flex;
}

.card-slot {
  width: 25rem;
  max-width: 95vw;
  height: 32rem;
  max-height: 65vh;
  margin: 1rem 0;
  padding: 1.5rem;
  position: relative;
  overflow: hidden;
}

.card-slot__counter {
  text-align: center;
  color: #999;
  background: #fff;
  border-radius: .7rem;
  height: 4rem;
  padding: 1rem;
  display: block;
  position: absolute;
  bottom: -1rem;
  left: 0;
  right: 0;
  box-shadow: 0 -3px 1px #0000000d;
}

.card {
  color: #fff;
  background: #000;
  border: 2px solid #222;
  border-radius: 1rem;
  width: 100%;
  height: 100%;
  padding: 2em;
  transition: translate .5s;
  transform: translateY(0);
  box-shadow: 0 0 25px #00000080;
}

.card__question {
  margin-bottom: 1em;
  font-size: 2.2em;
  font-weight: bold;
  line-height: 1.3em;
}

.card__rules {
  padding: 0;
  list-style-position: inside;
}

.card--enter-start {
  transform: translateY(100%);
}

.card--enter-end {
  transform: translateY(0);
}

.card--enter {
  transition: all .3s;
}

.card--leave-start {
  transform: translateY(0);
}

.card--leave-end {
  transform: translateY(100%);
}

.card--leave {
  transition: all .2s;
}

.btn {
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 100%;
  width: 5rem;
  height: 5rem;
  margin: .5rem;
  padding: 0;
  font-size: 3rem;
  transition: transform .1s;
  box-shadow: 1px 1px 3px #0000001a;
}

@media (hover: hover) {
  .btn:hover {
    transition: transform .2s;
    transform: scale(1.2);
  }
}

.btn:active {
  transition: transform .1s;
  transform: scale(1.1);
}

.credits {
  margin: 1rem 0;
  font-size: .7rem;
  bottom: 1em;
  right: 1em;
}

.credits__logo {
  vertical-align: middle;
  margin: 0 .2rem;
  display: inline-block;
}

